<template>
<div>
    <div class="shopcart">
        <div class="content" @click="toggleList">
            <div class="content-left">
                <div class="logo-wrapper">
                    <div class="logo" :class="{'hightlight':totalCount>0}">
                        <i class="icon-shopping_cart"  :class="{'hightlight':totalCount>0}"></i>
                    </div>
                    <div class="num" v-show="totalCount>0">{{totalCount}}</div>
                </div>
                <div class="price" :class="{'hightlight':totalCount>0}">￥{{totalPrice}}</div>
                <div class="desc">另需配送费￥{{deliveryPrice}}元</div>
            </div>
            <div class="content-right" @click.stop.prevent="pay">
                <div class="pay" :class="payClass" >
                    {{payDesc}}
                </div>
            </div>
        </div>
        <!-- 加购物车小球动画 未实现 -->
        <!-- <div class="ball-container">
            <div class="ball" v-for="(ball,index) in balls" v-show="ball.show" :key="index" ></div>
            <div class="inner"></div>
        </div> -->
        <div class="shopcart-list" v-show="listShow">
            <div class="list-header">
                <h1 class="title">购物车</h1>
                <span class="empty" @click="empty">清空</span>
            </div>
            <div class="list-content" ref="listcontent">
                <ul>
                    <li class="food" v-for="(food,index) in selectFoods" :key="index">
                        <span class="name">{{food.name}}</span>
                        <div class="price">
                            <span>￥{{food.price * food.count}}</span>
                        </div>
                        <div class="cartcontrol-wrapper">
                            <Cartcontrol :food="food"></Cartcontrol>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
    <div class="list-mask" v-show="listShow" @click="hideList"></div>
 </div>
</template>

<script>
import BScroll from "better-scroll";
import Cartcontrol from "./../cartcontrol/Cartcontrol";
 export default {
   data () {
     return {
         balls:[
             {
                show:false
            },
            {
                show:false
            },
            {
                show:false
            },
            {
                show:false
            },
            {
                show:false
            }
         ],
         fold:true
     };
   },
   props:{
       selectFoods:{
           type: Array,
           default(){
               return [
                //    {
                //     price:10,
                //     count:2
                //    }
               ];
           }
       },
       deliveryPrice:{
           type:Number,
           default:0
       },
       minPrice:{
           type:Number,
           default:0
       }
   },
   computed:{
       listShow(){
           if(!this.totalCount){
               this.fold=true;
               return false;
           }
           let show = !this.fold;
           if(show){
               this.$nextTick(()=>{
                   if(!this.scroll){
                       this.scroll=new BScroll(this.$refs.listcontent,{click:true});
                   }else{
                       this.scroll.refresh();
                   }
               });
           }
           return show;
       },
       totalPrice(){
           let total=0;
           this.selectFoods.forEach((food) => {
               total += food.price * food.count;
           });
           return total;
       },
       totalCount(){
           let count=0;
           this.selectFoods.forEach((food) => {
               count +=  food.count;
           });
           return count;
       },
       payDesc(){
           if(this.totalPrice === 0){
               return `￥${this.minPrice}元起送`;
           }else if(this.totalPrice<this.minPrice){
               let diff=this.minPrice-this.totalPrice;
               return `还差￥${diff}元起送`;
           }else{
               return "去结算";
           }
       },
       payClass(){
           if(this.totalPrice<this.minPrice){
               return "no-enough";
           }else{
               return "enough";
           }
       }

   },
   components: {
       Cartcontrol
   },
   methods:{
       toggleList(){
           if(!this.totalCount){
               return;
           }
           this.fold = !this.fold;
       },
       hideList(){
           this.fold=true;
       },
       empty(){
         this.selectFoods.forEach((food) =>{
             food.count=0;
         });  
       },
       pay(){
           if(this.totalPrice<this.minPrice){
               return;
           }
           window.alert(`支付${this.totalPrice}元`);
       }
   }
 };
</script>

<style lang="stylus" scoped>
@import '../../common/stylus/mixin'
.shopcart
    position fixed
    left 0
    bottom 0
    z-index 50
    width 100%
    height 48px
    background #000
    .content
        display flex
        background #141d27
        font-size 0
        .content-left
            flex 1
            .logo-wrapper
                display inline-block
                position relative
                top -10px
                margin 0 12px
                padding 6px
                width 56px
                height 56px
                box-sizing border-box
                vertical-align top
                border-radius 50% //圆形
                background #141d27
                .logo
                    width 100%
                    height 100%
                    border-radius 50%
                    background #2b343c
                    text-align center
                    &.hightlight
                        background rgb(0,160,220)
                    .icon-shopping_cart
                        line-height 44px
                        font-size 24px
                        color #80858a
                        &.hightlight
                            color  #ffffff
                .num
                    position absolute
                    top 0
                    right 0
                    width 24px
                    height 16px
                    line-height 16px
                    text-align center
                    border-radius 16px
                    font-size 9px
                    font-weight bold
                    color #fff
                    background rgb(240,20,20)
                    box-shadow 0px 4px 8px 0px rgba(0,0,0,0.4)
            .price
                display inline-block
                vertical-align top
                margin-top 12px
                line-height 24px
                padding-right 12px
                box-sizing border-box
                border-right 1px solid rgba(255,255,255,0.1) 
                font-size 16px
                font-weight 700
                color rgba(255,255,255,0.4)
                &.hightlight
                    color white
            .desc
                display inline-block
                vertical-align top
                line-height 24px
                margin 12px 0 0 12px
                color rgba(255,255,255,0.4)
                font-size 10px
        .content-right
            flex 0 0 105px
            width 105px
            .pay
                height 48px
                line-height 48px
                text-align center
                font-size 12px
                color rgba(255,255,255,0.4)
                font-weight bold
                background  #2b333b
                &.not-enough
                    background #2b333b
                &.enough
                    background #00b43c
                    color #ffffff
    .shopcart-list
        position absolute
        bottom  48px
        left 0
        z-index -1
        width 100%
        .list-header
            height 40px
            line-height 40px
            padding 0 18px
            background #f3f5f7
            border-bottom 1px solid rgba(1,17,27,0.1)
            .title
                float left
                font-size 14px
                color rgb(7,17,27)
            .empty
                float right 
                font-size 14px
                color rgb(0, 160, 220)
        .list-content
            padding 0 18px
            max-height 217px
            background #ffffff
            overflow hidden
            .food
                position relative
                padding 12px 0
                box-sizing border-box
                border-1px(rgba(7,17,27,0.1))
                .name
                    line-height 24px
                    font-size 14px
                    color rgb(7,17,27)
                .price
                    position absolute
                    right 90px
                    bottom 12px
                    line-height 24px
                    font-size 14px
                    font-weight bold
                    color rgb(240,20,20)
                .cartcontrol-wrapper
                    position absolute
                    right 0
                    bottom 0
                    line-height 24px
.list-mask
    position fixed
    top 0
    left 0
    width 100%
    height 100%
    z-index 40
    backdrop-filter blur(10px)
    background rgba(7,17,27,0.6)


</style>
